.Header{
    display: flex;
    align-items: center;
    width: 100%;
    height: 10%;
    color: white;
}

.Header .left{
    width: 30%;
}

.Header .left img{
    width: 100%;
}

.Header .middle{
    width: 60%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: #cf2c31;
}

.Header .middle .left{
    width: 75%;
    height: 100%;
    display: flex;
    align-items: center;
}

.Header .middle .right{
    width: 25%;
    height: 80%;
}

.Header .middle .left p,.Header .middle .right span{
    font-size: larger;
    padding: 10px;
}

.Header .middle .right span{
    display: flex;
    align-items: center;
}

.Header > .right{
    width: 10%;
    height: 100%;
    background-color: #071b8b;
}

.Header .right p{
    width: 100%;
    height: 50%;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: larger;
}

@media only screen and (max-width: 600px) {

    .Header .middle,
    .Header .right {
        display: none;
    }

    .Header .left{
        width: 100%;
    }

}